.basket-modal__shown {
  position: absolute;
  width: $mobile-width-only;
  padding-top: 36px;
  padding-bottom: 46px;
  padding-left: 30px;
  padding-right: 30px;
  text-align: center;
  box-shadow: 0 2px 2px 0 #c6c6c6;
}

.basket-modal__title {
  font-size: 25px;
  text-transform: uppercase;
  width: 160px;
  margin: 0 auto;
}

.basket-modal__size-select {
  font-size: 15px;
}

.basket-modal__size-wrapper {
  display: flex;
  justify-content: space-between;
}

.basket-modal__btn--size {
  width: 78px;
  border-color: rgba(0, 0, 0, 0.1);

  &:hover {
    border-color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
    color: #444444;
  }

  &:active {
    border-color: rgba(99, 209, 187, 1);
  }

  &--S {
    border-color: rgba(99, 209, 187, 0.9);
  }
}

.button--add {
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  width: 100%;
  height: 52px;
  margin-top: 30px;
  background-color: rgba(99, 209, 187, 0.9);
  border-color: rgba(99, 209, 187, 0.9);
}

@media (min-width: $tablet-width) {
  .basket-modal__shown {
    width: 566px;
    padding-left: 108px;
    padding-right: 108px;
  }

  .basket-modal__btn--size {
    flex-grow: 1;

    &--S {
      margin-right: 7px;
    }

    &--M {
      margin-right: 7px;
    }
  }
}
