.button {
  display: block;
  padding-top: 9px;
  padding-bottom: 8px;
  text-align: center;
  font-size: inherit;
  line-height: inherit;
  color: #444444;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #444444;
  background-color: #ffffff;
  cursor: pointer;

  &:hover {
    color: #ffffff;
    background-color: #444444;
  }

  &:active {
    color: rgba(255, 225, 255, 0.7);
  }
}
